'strict mode';

window.addEventListener('DOMContentLoaded', () => {
    // Creating new todo

    function card() {
        const input = document.querySelector('.add-item__input'),
            list = document.querySelector('.items__list');

        class TodoItem {
            constructor(text, className, classParent, isComplete = false) {
                this.text = text;
                this.isComplete = isComplete;
                this.className = className;
                this.parent = document.querySelector(classParent);
            }

            render() {
                const todoCard = document.createElement('div');
                todoCard.classList.add(this.className); 
                todoCard.innerHTML = `
                    <label class="checkbox__container">
                        <input type="checkbox" checked="checked">
                        <span class="checkmark"></span>
                        <div>${this.text}</div>   
                    </label>
                    <img src="./assets/img/icons/close_white.png" alt="close">
                `;
                this.parent.appent(todoCard);
            }
        }

        const first = new TodoItem(
            'first',
            'item',
            '.items__list'
        ).render();

        console.log(first);
    }

    card();












});